<template>
    <main id="c_send" class="container">
        <section class="search-area" @keyup.enter="search">
            <el-input class="search-group" v-model="key.keyword" placeholder="请输入姓名或注册证号或证件号或隶属机构" @change="search">
                <el-select style="width:120px;" slot="prepend" filterable v-model="key.rank" placeholder="棋手级别" @change="search">
                    <el-option-group v-for="group in optionGroups" :key="group.label" :label="group.label">
                        <el-option v-for="item in group.options" :label="item.label" :value="item.value" :key="item.value"></el-option>
                    </el-option-group>
                </el-select>
                <el-button slot="append" icon="search" @click="search" :disabled="isSearch" :loading="isSearch">查询</el-button>
            </el-input>
        </section>
        <div class="addMem">
            <div style="margin-right:20px;text-align:right">
                <div class="refresh">
                    <i class="fa fa-refresh" @click="getData"></i>
                </div>
            </div>
            <section class="add-area">
                <el-table
                        :height="tableHei"
                        :data="allMemberInfo"
                        stripe
                        :loading="isAjax"
                        @cell-click="showOrgDetail">
                    <el-table-column prop="index" label="序号" fixed></el-table-column>
                    <el-table-column prop="name" label="姓名" fixed></el-table-column>
                    <el-table-column prop="cretID" label="注册证号" width="120" fixed></el-table-column>
                    <el-table-column prop="cardType" label="证件类型" width="120"></el-table-column>
                    <el-table-column prop="ID" label="证件号" width="190">
                        <!--<template scope="scope">{{ scope.row.ID }}</template>-->
                    </el-table-column>
                    <el-table-column prop="rank" label="级别" min-width="70"></el-table-column>
                    <el-table-column prop="ranking" label="名次" min-width="100"></el-table-column>
                    <el-table-column prop="time" label="发证时间" min-width="176"></el-table-column>
                    <el-table-column prop="org" label="隶属机构" min-width="180">
                        <template scope="scope">
                            <span class="org-name">{{scope.row.org}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="title" label="赛事名称" min-width="200"></el-table-column>
                    <el-table-column prop="group" label="组别" min-width="160"></el-table-column>
                </el-table>
                <el-pagination class="text-center"
                               @size-change="handleSizeChange"
                               @current-change="handleCurrentChange"
                               :current-page="currentPage"
                               :page-sizes="[20, 50, 100]"
                               :page-size="pageSize"
                               layout="total,sizes, prev, pager, next, jumper"
                               :total="totalNum">
                </el-pagination>
            </section>
            <el-dialog class="orgDetail" size="tiny" title="机构信息" v-model="orgDetailVis">
                <div class="orgDetail-item">机构名称：{{orgDetail.orgName}}</div>
                <div class="orgDetail-item">联 系 人&nbsp;：{{orgDetail.linkMan}}</div>
                <div class="orgDetail-item">机构地址：{{orgDetail.address}}</div>
                <div class="orgDetail-item">联系电话：{{orgDetail.telNum}}</div>
            </el-dialog>
        </div>
    </main>
</template>
<script>
    export default{
        data(){
            let hei = 0,win = window.outerWidth;
            if(win>1366){
                hei=590;
            }else if(win<=1366&&win>=1024){
                hei=450;
            }else{
                hei=300;
            }
            return {
                key:{
                    keyword:'',
                    rank:''
                },
                isAjax:false,
                isSearch:false,
                isEdit:false,
                tinyAjax:false,
                dialogFormVisible:false,
                picURL:'',
                sameID:false,
                tableHei:hei,
                currentPage:1,
                pageSize:20,
                totalNum:0,
                pickerOptions:{
                    disabledDate(time){
                        return time.getTime() >= this.$store.state.sysTime - 8.64e7;
                    }
                },
                gotAge:'',
                gotGender:1,
                allMemberInfo:[],
                optionGroups:[
                    {
                        label:'全部',
                        options:[{value:'',label:'全部'}]
                    },
                    {
                        label:'级别',
                        options:[
                            {value:143,label:'28级'},
                            {value:142,label:'27级'},
                            {value:141,label:'26级'},
                            {value:99,label:'25级'},
                            {value:103,label:'24级'},
                            {value:104,label:'23级'},
                            {value:106,label:'22级'},
                            {value:107,label:'21级'},
                            {value:108,label:'20级'},
                            {value:109,label:'19级'},
                            {value:110,label:'18级'},
                            {value:111,label:'17级'},
                            {value:112,label:'16级'},
                            {value:113,label:'15级'},
                            {value:114,label:'14级'},
                            {value:115,label:'13级'},
                            {value:116,label:'12级'},
                            {value:117,label:'11级'},
                            {value:118,label:'10级'},
                            {value:119,label:'9级'},
                            {value:120,label:'8级'},
                            {value:121,label:'7级'},
                            {value:122,label:'6级'},
                            {value:123,label:'5级'},
                            {value:124,label:'4级'},
                            {value:125,label:'3级'},
                            {value:126,label:'2级'},
                            {value:127,label:'1级'}
                        ]
                    },
                    {
                        label:'业余段位',
                        options:[
                            {value:128,label:'1段'},
                            {value:129,label:'2段'},
                            {value:130,label:'3段'},
                            {value:131,label:'4段'},
                            {value:132,label:'5段'},
                            {value:133,label:'6段'}
                        ]
                    }
                ],
                orgDetailVis:false,
                orgDetail:{
                    orgName:'',
                    telNum:'',
                    linkMan:'',
                    address:'',
                },
            }
        },
        methods:{
            getCardTypeName (num){
                switch (num>>0){
                    case 1:
                        return '身份证';
                    case 2:
                        return '护照';
                    case 4:
                        return '台胞证';
                    case 8:
                        return '港澳通行证';
                }
            },
            showOrgDetail(row,column,cell){
                const that = this;
                if( cell.cellIndex === 7 ){
                    that.orgDetailVis = true;
                    that.orgDetail = {
                        orgName : row.org,
                        telNum  : row.telNum,
                        linkMan : row.linkMan,
                        address : row.address,
                    }
                }
            },
            handleSizeChange(pageSize){
                let that = this;
                that.currentData=[];
                that.pageSize = pageSize;
                that.currentPage = 1;
                that.getData();
            },
            handleCurrentChange(page){
                let that = this;
                that.currentPage = page;
                that.getData();
            },
            search(){
                this.getData();
            },
            getRank(rank){
                rank = rank >>0;
                this.optionGroups.forEach((e,i)=>{
                    e.options.forEach((e,i)=>{
                        if(e.value===rank){
                            rank = e.label;
                            return false;
                        }
                    });
                });
                return rank;
            },
            getAge(idCard){
                if(idCard.length===15){
                    this.gotGender = idCard[14]%2===0?'2':'1';
                    let _year = '19'+idCard.substring(6,8);
                    this.gotAge = ((new Date() - new Date(_year.substring(0,4),_year.substring(4,6)-1,_year.substring(6,8)))/1000/60/60/24/365)>>0;
                }else if(idCard.length===18){
                    this.gotGender = idCard[16]%2===0?'2':'1';
                    let _year = idCard.substring(6,14);
                    this.gotAge = ((new Date() - new Date(_year.substring(0,4),_year.substring(4,6)-1,_year.substring(6,8)))/1000/60/60/24/365)>>0;
                }
            },
            checkFormID(id){
                let that = this;
                that.sameID = false;
                that.allMemberInfo.forEach((e,i)=>{
                    if(e.ID===that.memberInfo.ID){
                        that.$message.warning('添加人员的证件号重复，请检查');
                        that.sameID=true;
                    }
                });
            },
            getData(){
                const that = this;
                that.isAjax = true;
                that.$ajax({
                    url:that.$store.state.total.cre.child.creList[1],
                    params:{
                        page:that.currentPage,
                        pageSize:that.pageSize,
                        level:that.key.rank,
                        keywords:that.key.keyword,
                        print_state:1
                    }
                }).then(res=>{
                    const _data = res.data;
                    that.isAjax = false;
                    if((_data.errcode>>0)===0){
                        const dat = _data.data;
                        that.allMemberInfo = [];
                        dat.forEach((e,i)=>{
                            // get county name
                            const _county =  that.$parent.cityQuery(e.county,'county','get');
                            // set all data
                            that.allMemberInfo.push({
                                index   : e.ROW_NUMBER,
                                cretID  : e.book_no,
                                ID      : e.idCard,
                                cardType: that.getCardTypeName(e.cardType>>0),
                                id      : e.logs_id,
                                name    : e.idCardName,
                                rank    : that.getRank(e.level_id>>0),
                                time    : e.update_time===null?'暂无':e.update_time.substring(0,19),
                                ranking : e.ranking===null?'暂无名次':e.ranking,
                                price   : e.price,
                                title   : e.title,
                                org     : e.org_name,
                                group   : e.groupName,
                                address : _county[0] +' '+ _county[1] + ' ' + _county[2] + ' ' + e.address,
                                telNum  : e.link_phone,
                                linkMan : e.link_name
                            });
                        });
                        that.totalNum = (_data.total>>0);
                    }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        this.$message.warning(_data.msg);
                    }
                },res=>{
                    that.isAjax = false;
                    this.$message.warning('网络开小差了');
                });
            },
            findIndex(id){
                let a='';
                this.allMemberInfo.forEach((e,i)=>{
                    if(e.id===id){
                        a = i;
                        return false;
                    }
                });
                return a;
            },
            formatDate(t){
                const _t = new Date(t);
                const f=(a)=>{
                    return a<10?'0'+a:a;
                };
                return _t.getFullYear()+'-'+f(_t.getMonth()+1)+'-'+f(_t.getDate())+' '+f(_t.getHours())+':'+f(_t.getMinutes());
            },
            confirmAdd(formName){
                const that = this;
                that.$refs[formName].validate((valid) => {
                    if(valid){
                        if(that.isEdit){
                            that.edit();
                        }else{
                            that.add();
                        }
                    }
                });
            },
        },
        mounted(){
            this.getData();
        }
    }
</script>
<style>
    #c_send .orgDetail .orgDetail-item{
        font-size:14px;
        margin-bottom:10px;
    }
    #c_send .text-red{
        color:#ff4949;
        margin-left:16px;
    }
    #c_send .text-red:hover{
        opacity:0.7;
    }
    #c_send .tip{
        color:#8492A6;
        font-size:12px;
        margin:4px 20px;
        text-align:right;
    }
    #c_send .upload-img{
        width:100%;
        height:100%;
    }
    #c_send .addMem{
        overflow: hidden;
        padding:20px;
    }
    #c_send .addMem .head-title p{
        margin:10px 0;
    }
    #c_send .add-area{
        margin-bottom:20px;
    }
    #c_send .add-panel{
        padding:15px;
        cursor:pointer;
        border-bottom:1px solid #d3dce6;
    }
    #c_send .add-panel:last-of-type{
        border-bottom:0;
    }
    #c_send .org-name{
        cursor:pointer;
        display:block;
        width:100%;
    }
    #c_send .add-panel .plus{
        display: inline-block;
        width:48px;
        height:48px;
        font-size:68px;
        line-height:38px;
        color:#fff;
        background: #999;
        margin-right:18px;
    }
    #c_send .add-panel .mem-info{
        position: relative;
        top:-16px;
        color:#999;
    }
    #c_send .mem-submit{

    }
    #c_send .mem-dialog{
        min-width:500px;
    }
    #c_send .mem-dialog .mem-form .form-input{
        width:300px;
    }
    #c_send .mem-dialog .mem-form .form-input.age{
        width:100px;
    }
    #c_send .refresh{
        display:inline-block;
        margin:10px 0;
    }
    #c_send .refresh .fa-refresh{
        color:#20a0ff;
        margin-right:6px;
        cursor:pointer;
    }
    #c_send .refresh .fa-refresh:hover{
        opacity:0.7;
    }
    #c_send .search-area{
        padding:20px;
        background:#fff;
        -webkit-box-shadow: 0 0 10px 0 #ddd;
        -moz-box-shadow: 0 0 10px 0 #ddd;
        box-shadow: 0 0 10px 0 #ddd;
    }
    #c_send .el-icon-more {
        margin-left:14px;
    }
    /*.search-area .el-input-group__append button.el-button{
        color:#fff;
        background-color:#20a0ff;
        border-color:#20a0ff;
        border-radius:0;
    }*/
    #c_send .search-area .add-new{
        margin-left:20px;
    }
    #c_send .search-area .search-group{
        width:80%;
    }
</style>


