<template>
    <main id="bind" class="container">
        <header class="head-title">
            <p>
                <span>{{title}}云比赛账号</span>
            </p>
        </header>
        <div></div>
        <el-form ref="info" :model="info" :rules="rules" label-position="left" label-width="108px">
            <el-form-item prop="loginName" label="已绑定用户名" v-show="bind">
                <el-input placeholder="请输入云比赛账号" :disabled="bind" v-model="loginName" :maxlength="50"></el-input>
            </el-form-item>
            <el-form-item prop="account" label="云比赛账号">
                <el-input placeholder="请输入云比赛账号" :disabled="bind" v-model="info.account"></el-input>
            </el-form-item>
            <el-form-item prop="pwd" v-if="!bind" label="云比赛密码">
                <el-input type="password" auto-complete="new-password" v-model="info.pwd" placeholder="请输入密码"></el-input>
            </el-form-item>
                <p class="bind-tip" v-if="!bind && $store.state.total.set.child.signUpYunbisai[1]">还没有云比赛机构账号？<span class="text-blue" @click="signUp">点此注册</span></p>
                <p class="tip" v-if="!bind">提示：绑定云比赛机构账号后才可以使用赛事功能。（请使用主账号绑定）</p>
            <el-form-item>
                <el-button v-if="!bind" type="primary" @click="bindAccount" :disabled="isAjax" :loading="isAjax">{{title}}</el-button>
            </el-form-item>
        </el-form>
        <el-dialog title="解绑机构账号" v-model="visible" class="text-center" size="tiny">
            <p class="dialog-title">是否确认解绑账号?</p>
            <p class="dialog-tip">解绑后将无法使用赛事管理功能。可再次绑定</p>
            <el-button size="small" @click="visible=false">取消</el-button>
            <el-button size="small" type="primary" @click="unbindAccount" :disabled="isAjax" :loading="isAjax">确认</el-button>
        </el-dialog>
    </main>
</template>
<script>
    export default{
        data(){
            return {
                visible:false,
                title:'绑定',
                isAjax:false,
                bind:false,
                loginName:'',
                info:{
                    account:'',
                    pwd:''
                },
                rules:{
                    account:[
                        {required:true,message:'请输入账号',trigger:'blur'},
                        {min:3,max:50,message:'长度在3-50个字符',trigger:'blur'}
                    ],
                    pwd:[
                        {required:true,message:'请输入密码',trigger:'blur'},
                        {min:6,max:18,message:'长度在6-18个字符',trigger:'blur'}
                    ]
                }
            }
        },
        methods:{
            bindAccount(){
                const that = this;
                const __url = that.$store.state.total.set.child.bind[1];
                if(__url==='') return false;
                if(!that.bind){
                    that.isAjax = true;
                    that.$ajax({
                        url:__url,
                        params:{
                            name:that.info.account,
                            pwd:that.info.pwd
                        }
                    }).then(res=>{
                        that.isAjax = false;
                        const _data = res.data;
                        if((_data.errcode>>0)===0){
                            that.$message.success(_data.msg);
                            that.bind = true;
                            that.text = '云比赛用户名';
                            that.$parent.refreshToken();
                        }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                            that.$parent.overTime();
                        }else{
                            that.$message.warning(_data.msg);
                        }
                    },res=>{
                        that.isAjax = false;
                        that.$message.warning('网络似乎出了点问题');
                    });
                }else{
                    that.visible = true;
                }
            },
            unbindAccount(){
                const that = this;
                const __url = that.$store.state.total.set.child.unbind[1];
                if(__url==='') return false;
                that.isAjax = true;
                if(that.bind){
                    that.$ajax({
                        url:__url,
                        params:{
                            pwd:that.info.pwd
                        }
                    }).then(res=>{
                        that.isAjax = false;
                        that.visible= false;
                        const _data = res.data;
                        if((_data.errcode>>0)===0){
                            that.$message.success(_data.msg);
                            that.bind = false;
                            that.title = '绑定';
                            that.info = {
                                account:'',
                                pwd:''
                            };
                            that.$parent.refreshToken();
                        }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                            that.$parent.overTime();
                        }else{
                            that.$message.warning(_data.msg);
                        }
                    },res=>{
                        that.isAjax = false;
                        that.visible= false;
                        that.$message.warning('网络似乎出了点问题');
                    });
                }
            },
            signUp(){
                const that = this;
                const __url = that.$store.state.total.set.child.signUpYunbisai[1];
                if(__url==='') return false;
                that.$ajax({
                    url:__url
                }).then(res=>{
                    const _data = res.data;
                    if((_data.errcode>>0)===0){
                        that.$parent.openNewTag();
                        window.open('http://www.yunbisai.com/orgRegister?t=' + _data.msg);
                    }else if((_data.errcode>>0)===1000 || (_data.errcode>>0)===1001){
                        that.$parent.overTime();
                    }else{
                        that.$message.warning(_data.msg);
                    }
                },res=>{
                   that.$message.warning('网络似乎出了点问题');
                });
            }
        },
        mounted(){
            const that = this;
            const __url = that.$store.state.total.set.child.bindInfo[1];
            if(__url==='') return false;
            that.$ajax({
                url:__url,
            }).then(res=>{
                const _data = res.data;
                if((_data.errcode>>0)===0){
                    const dat = _data.data;
                    if(dat!==null){
                        that.info.account = dat.loginEmail;
                        that.loginName = dat.loginName;
                        that.bind = true;
                        //that.title = '解绑';
                    }
                }else if((_data.errcode>>0)===1000||(_data.errcode>>0)===1001){
                    that.$parent.overTime();
                }else{
                    that.$message.warning(_data.msg);
                }
                },res=>{
               that.$message.warning('网络似乎出了点问题');
            });
        }
    }
</script>
<style>
    #bind .text-blue{
        color:#20A0FF;
        cursor:pointer;
    }
    #bind .text-blue:hover{
        opacity:0.8;
    }
    #bind .bind-tip{
        font-size:16px;
        text-align:left;
    }
    #bind{
        background:#fff;
        padding:10px 20px;
    }
    #bind .el-form{
        width:50%;
        max-width:400px;
        text-align:center;
    }
    #bind .tip{
        font-size:12px;
        margin-bottom:6px;
        text-align:left;
    }
    #bind .dialog-title{
        font-size:14px;
    }
    #bind .dialog-tip{
        font-size:12px;
        color:#999;
        margin:10px;
    }
</style>


